import kfIcon from "../../../assets/kfInfo.png";
import hotIcon from "../../../assets/hot.png";
import jiant from "../../../assets/jiant.png";
import loading from "../../../assets/loading.gif";
export default {
  data() {
    return {
      loading: loading,
      isShow: true,
      kfIcon: kfIcon,
      jiant: jiant,
      hotIcon: hotIcon,
      list: [],
       showPositionValue: false, //提示信息
      toastContent: ""
    };
  },
  mounted() {
    this.getActivity();
  },
  methods: {
    getActivity() {
      var that = this,
        formData = {
          sign: that.$store.state.user.sign,
          access_token: that.$store.state.user.token
        };
      that
        .$request({
          method: "POST",
          url: "/abacus/activity",
          data: that.$qs.stringify(formData)
        })
        .then(function(response) {
          if (response.code == 0) {
            that.isShow = false;
            that.list = response.data;
            // response.data.forEach(val => {
            //   val.img = "http://img.hashsl.com/" + val.img;
            // });
          }
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    // 客服页面
    getServiceInfo() {
      this.$router.push("/serviceInfo");
    },
    // 热度
    getHot(val, img, link) {
      var that = this,
        formData = {
          sign: that.$store.state.user.sign,
          access_token: that.$store.state.user.token,
          activity_id: val
        };
      that
        .$request({
          method: "POST",
          url:"/abacus/activity-hot",
          data: that.$qs.stringify(formData)
        })
        .then(function(response) {
          if (response.code == 0) {
            if (val== 1) {
              that.$router.push("/activityH5");
            } else {
              window.location.href = link;
            }
          } else if(response.code ==1012){
            that.$router.push("/login");
          }else{
            that.showPositionValue = true;
            that.toastContent = "response.errmsg";
          }
        })
        .catch(function(error) {
          console.log(error);
        });
    }
  }
};



